export default {
    name: "Mine",
    data() {
        return {
            shareModal: false,
            clipboard: '我正在用畅游看吧看免费百万小说。下载地址：http://www.xxx.com',
            displayInfo: [
                {
                    icon: 'appreciate',
                    title: '请给我好评'
                },
                {
                    icon: 'share',
                    title: '分享给朋友'
                },
                {
                    icon: 'comment',
                    title: '意见反馈'
                },
                {
                    icon: 'settings',
                    title: '设置'
                }
            ]
        }
    },
    methods: {
        tapBtn(type) {
            switch (type) {
                case 'appreciate':
                    break;
                case 'share':
                    this.shareModal = true;
                    break;
                case 'comment':
                    break;
                case 'settings':
                    break;
            }
        },
        hideShareModal(type) {
            debugger;
            this.shareModal = false;
            if (!type) {
                // 复制到剪切板
                uni.setClipboardData({
                    data: this.clipboard,
                    success: function () {
                        console.log('success');
                    }
                });
            }
        }
    }
}
